@use "base/variables" as v;

.portada {
    margin-top: 2.5rem;
    min-height: 43rem;
    position: relative;
}

.contenedorUno{
    display: flex;
    justify-content: center;
    align-items: center;
    //background-image: url(../img/teclado_portada.webp);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    min-height: 20rem;
    margin: 0rem 0 3rem 0;

    &__heading {
        color: v.$blanco;
        font-family: v.$fuentePrincipal;
        font-size: 2.2rem;
        position: absolute;
        width: 37rem;
        margin: 0;
    }
}

.contenedorDos{
    display: flex;
    align-items: center;
    background-image: url(../img/teclado1.webp);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    min-height: 20rem;
    margin: 3rem 0 0 0;

    &__heading {
        background-color: hsl(0deg 3% 73% / 59%);
        color: v.$blanco;
        font-family: v.$fuentePrincipal;
        font-size: 2.2rem;
        margin: 2rem;
        padding: 1rem;
        position: absolute;
        width: 25rem;

        .cta {
            border: none;
            background: none;
            cursor: pointer;
            position: relative;
            z-index: 1;
            margin: 1rem 0;
        }

        .cta span {
            padding-bottom: 7px;
            letter-spacing: 4px;
            font-size: 14px;
            padding-right: 15px;
            text-transform: uppercase;
        }

        .cta svg {
            transform: translateX(-8px);
            transition: all 0.3s ease;
        }

        .cta:hover svg {
            transform: translateX(0);
        }

        .cta:active svg {
            transform: scale(0.9);
        }

        .hover-underline-animation {
            position: relative;
            color: v.$negro;
            padding-bottom: 20px;
        }

        .hover-underline-animation:after {
            content: "";
            position: absolute;
            width: 100%;
            transform: scaleX(0);
            height: 2px;
            bottom: 0;
            left: 0;
            background-color: v.$negro;
            transform-origin: bottom right;
            transition: transform 0.25s ease-out;
        }

        .cta:hover .hover-underline-animation:after {
            transform: scaleX(1);
            transform-origin: bottom left;
        }
    }
}