body {
  font-family: Roboto Slab, serif;
  font-size: 1.6rem;
  background-color: #f7f7f7;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

.text-center {
  text-align: center;
}

.f-w-500 {
  font-weight: 500;
}

.f-w-700 {
  font-weight: 700;
}

.bg-img-inicio {
  background-image: url(../img/teclado_portada.webp);
}

.bg-img-teclado {
  background-image: url(../img/teclado_portada_2.webp);
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: Roboto Slab, serif;
}

h2 {
  font-size: 4rem;
  font-family: Roboto Slab, serif;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header__logo img {
  display: inline;
}
.header__nav a {
  color: rgb(54, 52, 52);
  text-transform: uppercase;
  font-size: 1rem;
  padding: 0 5px;
  text-decoration: none;
  font-family: Roboto Slab, serif;
}
.header__nav a.activo {
  color: #6366f1;
}
.header__nav a:hover {
  color: rgba(99, 101, 241, 0.6392156863);
  border-bottom: 1px solid rgb(121, 120, 120);
}

.portada {
  margin-top: 2.5rem;
  min-height: 43rem;
  position: relative;
}

.contenedorUno {
  display: flex;
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 20rem;
  margin: 0rem 0 3rem 0;
}
.contenedorUno__heading {
  color: #ffffff;
  font-family: Roboto Slab, serif;
  font-size: 2.2rem;
  position: absolute;
  width: 37rem;
  margin: 0;
}

.contenedorDos {
  display: flex;
  align-items: center;
  background-image: url(../img/teclado1.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 20rem;
  margin: 3rem 0 0 0;
}
.contenedorDos__heading {
  background-color: hsla(0, 3%, 73%, 0.59);
  color: #ffffff;
  font-family: Roboto Slab, serif;
  font-size: 2.2rem;
  margin: 2rem;
  padding: 1rem;
  position: absolute;
  width: 25rem;
}
.contenedorDos__heading .cta {
  border: none;
  background: none;
  cursor: pointer;
  position: relative;
  z-index: 1;
  margin: 1rem 0;
}
.contenedorDos__heading .cta span {
  padding-bottom: 7px;
  letter-spacing: 4px;
  font-size: 14px;
  padding-right: 15px;
  text-transform: uppercase;
}
.contenedorDos__heading .cta svg {
  transform: translateX(-8px);
  transition: all 0.3s ease;
}
.contenedorDos__heading .cta:hover svg {
  transform: translateX(0);
}
.contenedorDos__heading .cta:active svg {
  transform: scale(0.9);
}
.contenedorDos__heading .hover-underline-animation {
  position: relative;
  color: #000;
  padding-bottom: 20px;
}
.contenedorDos__heading .hover-underline-animation:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #000;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.contenedorDos__heading .cta:hover .hover-underline-animation:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.galeria__heading {
  padding: 1rem;
  border-bottom: 1px solid hsla(0, 3%, 73%, 0.59);
  font-size: 2.2rem;
  text-transform: uppercase;
  margin: 2rem 2rem;
}
@media screen and (min-width: 1024px) {
  .galeria__heading {
    margin: 2rem 30rem;
  }
}
.galeria__grid {
  margin: 1rem;
  padding: 0 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .galeria__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 1024px) {
  .galeria__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.galeria__grid .imagen {
  overflow: hidden;
}
.galeria__grid .imagen img {
  height: 30rem;
  width: 62rem;
  object-fit: cover;
}
@media screen and (min-width: 1024px) {
  .galeria__grid .imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .galeria__grid .imagen:nth-child(1) img {
    height: 62rem;
    object-position: center;
  }
  .galeria__grid .imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .galeria__grid .imagen:nth-child(2) img {
    width: 62rem;
  }
  .galeria__grid .imagen:nth-child(4) {
    grid-row: 2/4;
  }
  .galeria__grid .imagen:nth-child(4) img {
    height: 62rem;
    object-position: center;
  }
  .galeria__grid .imagen:nth-child(5) {
    grid-column: 3/5;
  }
  .galeria__grid .imagen:nth-child(5) img {
    width: 62rem;
  }
  .galeria__grid .imagen:nth-child(6) {
    grid-column: 3/5;
  }
  .galeria__grid .imagen:nth-child(6) img {
    width: 62rem;
  }
  .galeria__grid .imagen:nth-child(7) {
    grid-row: 3/5;
  }
  .galeria__grid .imagen:nth-child(7) img {
    height: 62rem;
  }
  .galeria__grid .imagen:nth-child(8) {
    grid-column: 2/5;
  }
  .galeria__grid .imagen:nth-child(8) img {
    width: 94rem;
  }
  .galeria__grid .imagen:nth-child(9) {
    grid-column: 1/3;
  }
  .galeria__grid .imagen:nth-child(9) img {
    width: 62rem;
  }
  .galeria__grid .imagen:nth-child(10) {
    grid-column: 3/5;
  }
  .galeria__grid .imagen:nth-child(10) img {
    width: 62rem;
  }
}
.galeria__grid .img:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
  transition: all 0.5s ease-in-out;
}

