  }
}
.footer__heading {
  margin: 2rem 0 0;
  font-size: 20px;
  color: rgba(99, 101, 241, 0.6392156863);
}
.footer p {
  color: #ffffff;
  font-size: 15px;
}
.footer__enlace {
  display: flex;
  flex-direction: column;
  padding: 1rem 0 0;
}
.footer__enlace a {
  color: #ffffff;
  padding: 1rem 0 0;
  font-size: 15px;
}
.footer__frase {
  background-color: #252525;
  color: #c1b8b8;
  font-size: 10px;
  padding: 10px;
}

.input-container {
  position: relative;
  margin: 20px 0 0;
}

.input-field {
  display: block;
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: none;
  border-bottom: 2px solid #ccc;
  outline: none;
  background-color: transparent;
  color: #b2b2b2;
}

.input-label {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 15px;
  color: rgba(204, 204, 204, 0);
  pointer-events: none;
  transition: all 0.3s ease;
}

.input-highlight {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 2px;
  width: 0;
  background-color: #007bff;
  transition: all 0.3s ease;
}

.input-field:focus + .input-label {
  top: -20px;
  font-size: 12px;
  color: #007bff;
}

.input-field:focus + .input-label + .input-highlight {
  width: 100%;
}